<template>
<div class="data-list">
  <div class="table-header-wrapper">
    <table class="table table-header" ref="headerWrapper">
      <thead>
        <tr>
          <th class="type">{{ $t('assets.fields.type') }}</th>
          <th class="name">{{ $t('assets.fields.name') }}</th>
          <th class="description">{{ $t('assets.fields.description') }}</th>
          <th
            class="validation"
            :style="{
              'border-left': '2px solid ' + column.color
            }"
            v-for="column in validationColumns">
            {{ column.name }}
          </th>

          <th class="actions">
            <button-link
              class="is-small"
              icon="plus"
              :text="$t('tasks.create_tasks')"
              :path="{
                name: 'create-asset-tasks',
                params: {
                  production_id: currentProduction.id
                }
              }"
            >
            </button-link>
          </th>
        </tr>
      </thead>
    </table>
  </div>


  <div class="table-body" v-scroll="onBodyScroll">
    <table class="table">
      <tbody>
        <tr
          key="entry.id"
          :class="{canceled: entry.canceled}"
          v-for="entry in entries"
        >
          <td :class="{type: !entry.canceled}">
            {{ entry.asset_type_name }}
          </td>
          <td :class="{name: !entry.canceled}">
            {{ entry.name }}
          </td>
          <td class="description">
            {{ entry.description }}
          </td>
          <validation-cell
            :key="column.name + '-' + entry.id"
            :column="column"
            :entity="entry"
            @select="onTaskSelected"
            @unselect="onTaskUnselected"
            v-for="column in validationColumns"
          >
          </validation-cell>
          <row-actions
            :entry="entry"
            :edit-route="{
              name: 'edit-asset',
              params: {
                production_id: currentProduction.id,
                asset_id: entry.id
              }
            }"
            :delete-route="{
              name: 'delete-asset',
              params: {
                production_id: currentProduction.id,
                asset_id: entry.id
              }
            }"
            :restore-route="{
              name: 'restore-asset',
              params: {
                production_id: currentProduction.id,
                asset_id: entry.id
              }
            }"
          >
          </row-actions>
        </tr>
      </tbody>
    </table>
  </div>

  <div class="has-text-centered" v-if="isLoading">
    <img src="../../assets/spinner.svg">
  </div>
  <div class="has-text-centered" v-if="isError">
    <span class="tag is-danger">An error occured while loading data</span>
  </div>

  <p class="has-text-centered nb-assets">
    {{ entries.length }} {{ $tc('assets.number', entries.length) }}
  </p>

</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import ProductionNameCell from '../cells/ProductionNameCell'
import RowActions from '../widgets/RowActions'
import ValidationCell from '../cells/ValidationCell'
import ButtonLink from '../widgets/ButtonLink'
import ButtonHrefLink from '../widgets/ButtonHrefLink'
import PageTitle from '../widgets/PageTitle.vue'

export default {
  name: 'asset-list',
  props: [
    'entries',
    'isLoading',
    'isError',
    'validationColumns'
  ],
  data () {
    return {}
  },
  components: {
    ButtonLink,
    ButtonHrefLink,
    RowActions,
    PageTitle,
    ProductionNameCell,
    ValidationCell
  },
  computed: {
    ...mapGetters([
      'currentProduction',
      'assetSearchValue',
      'selectedTasks'
    ])
  },
  methods: {
    ...mapActions([
    ]),
    onTaskSelected (task) {
      this.$store.commit('ADD_SELECTED_TASK', task)
    },
    onTaskUnselected (task) {
      this.$store.commit('REMOVE_SELECTED_TASK', task)
    },
    onBodyScroll (event, position) {
      this.$refs.headerWrapper.style.left = `-${position.scrollLeft}px`
    }
  }
}
</script>

<style scoped>
.table {
  min-width: 1000px;
}

.actions {
  min-width: 150px;
  padding: 0.4em;
}

.name {
  min-width: 200px;
  max-width: 200px;
  width: 200px;
  font-weight: bold;
}

.type {
  min-width: 120px;
  max-width: 120px;
  width: 120px;
  font-weight: bold;
}

.description {
  min-width: 200px;
  max-width: 200px;
  width: 200px;
}

.validation {
  min-width: 120px;
  max-width: 120px;
  width: 120px;
  margin-right: 1em;
}

td.name {
  font-size: 1.2em;
}

td.type {
  font-size: 1.2em;
}
</style>



